import React, { Component } from 'react';
import { Table } from 'antd';

export default class peopleListModal extends Component {
    state = {
        columns:[
            {
                title:"序号",
                dataIndex:"sortIndex",
                width:80
            },{
                title:"姓名",
                dataIndex:"name"
            },{
                title:"工号",
                dataIndex:"userName",
                render:(text)=><span title={text}>{text}</span>
            }
        ]
    }
    render() {
        // const dataSource = [
        //     {userId:"",name:"",userName:"",sortIndex:"1"}
        // ]
        const dataSource = this.props.dataSource;
        return (
            <Table
                rowKey="sortIndex"
                dataSource={dataSource}
                columns={this.state.columns}
            />
        )
    }
}
