import React, { Component } from 'react';
import { getAction,postAction } from '@/axios';
import moment from 'moment';
import { withRouter } from 'react-router-dom';
import { Card, Radio, Input, Steps, Timeline, message, Popover, Button,Descriptions,Modal } from 'antd';
import FormListModal from './formListModal';
import PeopleListModal from './peopleListModal';
import styles from './index.module.less'
import AuditForm from './auditForm';
import PreviewDoc from '@/pages/common/PreviewDoc';
const typeList = ["","填报计划审核","创建表单审核","填报结果审核"];
const auditStatusList = [
    {color:"#52C41A",text:"通过"},
    {color:"red",text:"退回"},
    {color:"#52C41A",text:"审核中"},
    {color:"orange",text:"待审批"}
]
function findStatus(text){
    let arr = auditStatusList.find(item => item.text===text);
    if(arr){
        return arr
    }
    return false;
}

//步骤条
const stepComponent = (data) => {
    let stepsData = data || {current:0,steps:[]};
    if(!stepsData.steps){
        return <div style={{textAlign:"center"}}>暂无数据</div>
    }
    return (
        <Steps current={stepsData.current} progressDot={true} style={{marginLeft:"-60px"}}>
            {
                stepsData.steps.map((item,index) => (
                    <Steps.Step key={index} title={item.title} description={
                        <Popover
                            content={
                                <Timeline>
                                    {
                                        item.toolTips?item.toolTips.map((el,index) => (
                                            <Timeline.Item key={index}>
                                                <p style={{color:"rgba(0,0,0,0.65)"}}>
                                                    {el.job}
                                                    <span style={{color:"#000000",padding:"0 5px"}}>{el.name}</span>
                                                    {
                                                        findStatus(el.status)?<span style={{color:findStatus(el.status).color}}>{findStatus(el.status).text}</span>:"状态未知"
                                                    }
                                                </p>
                                                {
                                                    (el.status==='通过' || el.status==='退回')?
                                                    <>
                                                        <p style={{color:"rgba(0,0,0,0.65)"}}>审批意见：<span>{el.comments}</span></p>
                                                        <p style={{color:"rgba(0,0,0,0.65)"}}>{moment.unix(el.time/1000).format("YYYY/MM/DD HH:mm:ss")}</p>
                                                    </>:null
                                                } 
                                            </Timeline.Item>
                                        )):null
                                    }
                                </Timeline>
                            }
                        >
                            <span style={{cursor:"pointer",color:"#3F54A2"}}>{item.detail}{item.detail?item.approveType===1?'(或签)':'(会签)':''}</span>
                        </Popover>
                    } />
                ))
            }
        </Steps>
    )
}

class Audit extends Component {
    constructor(props){
        super(props);
        this.state = {
            showPDF:false,//预览
            processNodeTaskId:"",
            record:{
                businessId:"",//预览用id
                nodeTaskName:"",//任务内容
                processType:1,//任务类型
                taskStartTime:"",//审核开始时间
                taskEndTime:"",//审核截止时间
                appNodeTaskStatus:2,//审核结论
                userComments:"",//审核意见
            },
            planInfo:{//计划详情
                name:"",
                startTime:"",
                endTime:"",
                attachment:[
                    // {planAttachmentName:"",planAttachmentId:""}
                ],
                formList:[],//表单列表
                peopleList:[],//人员列表
            },
            stepsData:null,
            showFormListModal:false,
            showPeopleListModal:false,
            lookVisible:false
        }
    }
    componentDidMount(){
        let url = window.location.toString();
        let arrObj = url.split("?");
        if(arrObj.length>1){
            let arrParam = arrObj[1].split("&");
            let arr;
            for(let i=0;i<arrParam.length;i++){
                arr = arrParam[i].split("=");
                if(arr !==null && arr[0] === "id"){
                    this.setState({
                        processNodeTaskId:arr[1]
                    },this.getApproveNodeTaskRequest);
                    return;
                }
            }
        }
    }
    //根据id获取详情
    getApproveNodeTaskRequest = () => {
        postAction("/ybt/wf/approveTask/getApproveNodeTaskById",{processNodeTaskId:this.state.processNodeTaskId}).then(res => {
            if(res.success){
                this.setState({
                    record:{
                        nodeTaskName:res.obj.nodeTaskName,
                        processType:res.obj.processType,
                        taskStartTime:res.obj.taskStartTime,
                        taskEndTime:res.obj.taskEndTime,
                        appNodeTaskStatus:2,
                        userComments:res.obj.userComments,
                        businessId:res.obj.businessId,
                        businessType:res.obj.businessType,
                        processNodeTaskId:res.obj.processNodeTaskId,
                    },
                },()=>{this.queryOverTaskProcessRequest(res.obj.taskId,res.obj.processId)});
                if(res.obj.processType===1){//填报计划审核
                    this.queryPlanInfoRequest();
                }
            }else{
                console.log(res.obj)
            }
        })
    }
    //提交
    submit = () => {
        // console.log(this.state.record);
        let data = {
            businessId:this.state.record.businessId,
            businessType:this.state.record.businessType,
            processNodeTaskId:this.state.record.processNodeTaskId,
            appNodeTaskStatus:this.state.record.appNodeTaskStatus,
            userComments:this.state.record.userComments
        }
        if(this.state.record.appNodeTaskStatus===3 && !this.state.record.userComments){//审核退回必须要填写审核意见
            message.warning("审核退回必须要填写审核意见!");
            return false;
        }
        // console.log(data);
        postAction("/ybt/wf/approveTask/doApprove",data).then(res => {
            if(res.success){
                message.success("审核完成");
                // this.props.history.push("/auditManagement");
                this.props.history.goBack();
            }else{
                message.error(res.obj)
            }
        })
    }
    //获取流程图
    queryOverTaskProcessRequest=(taskId,processId)=>{
        let data={
            taskId,
            processId
        }
        getAction('ybt/planTask/queryProcessMap',data).then(res => {
            if(res.success){
                //setps
                let stepsData = {current:res.obj.current,steps:res.obj.setps}
                this.setState({
                    stepsData
                });
            }else{
                message.error(res.obj)
            }
        })
    }
    //获取计划详情
    queryPlanInfoRequest = () => {
        getAction("/ybt/planTask/queryPlanTaskByNodeId?processNodeTaskId="+this.state.processNodeTaskId).then(res => {
            if(res.success){
                this.setState({
                    planInfo:{
                        name:res.obj.planTaskName,
                        startTime:res.obj.taskStartTime,
                        endTime:res.obj.taskEndTime,
                        attachment:res.obj.attList,
                        formList:res.obj.formList.map((item,index) =>({sortIndex:index+1,...item})) || [],
                        peopleList:res.obj.userList.map((item,index) =>({sortIndex:index+1,...item})) || [],
                    }
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //预览
    _preview = () => {
        // message.info("预览") 
        this.setState({
            showPDF:true
        })
    }
    _look=()=>{
        this.setState({
            lookVisible:true
        })
    }
    handleCancelLook=()=>{
        this.setState({
            lookVisible:false
        })
    }
    //下载计划附件
    _downLoadAttach = (item) => {
        const planAttachmentId = item.planAttachmentId;
        window.open(`http://${window.location.host}/ybt/planTask/download/planAttachment?planAttachmentId=${planAttachmentId}`)
    }
    //修改审核结果
    changeResult = (e) => {
        this.setState({
            record:{
                ...this.state.record,
                appNodeTaskStatus:e.target.value
            }
        })
    }
    //修改审核意见
    changeInput = (e) => {
        this.setState({
            record:{
                ...this.state.record,
                userComments:e.target.value
            }
        })
    }
    
    
    render() {
        let {record} = this.state;
        return (
            <div className={styles.container}>
                <Card title="审核" bodyStyle={{padding:0}}>
                    <div style={{display:"flex"}}>
                        <div style={{flex:"2",overflow:'hidden'}}>
                            <Descriptions title="" column={1} bordered>
                            {
                                    record.processType===1?
                                    <Descriptions.Item label="填报计划名称">{this.state.planInfo.name}</Descriptions.Item>:null
                                }
                                {
                                     record.processType===1?
                                     <Descriptions.Item label="填报开始时间">{this.state.planInfo.startTime}</Descriptions.Item>:null
                                }
                                {
                                     record.processType===1?
                                     <Descriptions.Item label="填报结束时间">{this.state.planInfo.endTime}</Descriptions.Item>:null
                                }
                                {
                                     record.processType===1?
                                     <Descriptions.Item label="附件">
                                        {
                                            this.state.planInfo.attachment.map(item => (
                                                <div key={item.planAttachmentId}>{item.planAttachmentName} <span onClick={()=>{this._downLoadAttach(item)}} style={{textDecoration:"underLine",color:"blue",marginLeft:"20px",cursor:"pointer"}}>下载</span></div>
                                            ))
                                        }
                                    </Descriptions.Item>:null
                                }
                                 {
                                     record.processType===1?
                                     <Descriptions.Item label="填报表单">
                                         共{this.state.planInfo.formList.length}张 
                                        {this.state.planInfo.formList.length>0?
                                            <span onClick={()=>{this.setState({showFormListModal:true})}} style={{textDecoration:"underLine",color:"blue",marginLeft:"20px",cursor:"pointer"}}>查看</span>:null
                                        }
                                     </Descriptions.Item>:null
                                }
                                {
                                     record.processType===1?
                                     <Descriptions.Item label="填报人员">
                                        共{this.state.planInfo.peopleList.length}人
                                        {this.state.planInfo.peopleList.length>0?
                                            <span onClick={()=>{this.setState({showPeopleListModal:true})}} style={{textDecoration:"underLine",color:"blue",marginLeft:"20px",cursor:"pointer"}}>查看</span>:null
                                        }
                                     </Descriptions.Item>:null
                                }
                                <Descriptions.Item label="任务内容">
                                    {record.nodeTaskName}
                                    {
                                        record.processType===1?null:
                                        <React.Fragment>
                                            <span style={{color:"#24695C",cursor:"pointer",margin:"0 15px"}} onClick={this._preview}>预览</span>
                                            {
                                                record.processType===3 && <span style={{color:"#24695C",cursor:"pointer",margin:"0 15px 0 0"}} onClick={this._look}>查看</span>
                                            }
                                            {/* <a style={{color:"#24695C",cursor:"pointer"}} href={`http://${window.location.host}/ybt/form/dl/export?formTaskId=${record.businessId}`}>下载</a> */}
                                        </React.Fragment>
                                    }
                                </Descriptions.Item>
                                <Descriptions.Item label="任务类型">
                                    {typeList[record.processType]}
                                </Descriptions.Item>
                                <Descriptions.Item label="提交审核时间">
                                    {
                                        record.taskStartTime?moment(record.taskStartTime).format("YYYY-MM-DD HH:mm:ss"):""
                                    }
                                </Descriptions.Item>
                                <Descriptions.Item label="完成审核时间">
                                    {
                                        record.taskEndTime?moment(record.taskEndTime).format("YYYY-MM-DD HH:mm:ss"):""
                                    }
                                </Descriptions.Item>
                                <Descriptions.Item label="审核结论">
                                    <Radio.Group onChange={this.changeResult} value={record.appNodeTaskStatus}>
                                        <Radio value={2}>通过</Radio>
                                        <Radio value={3}>退回</Radio>
                                    </Radio.Group>
                                </Descriptions.Item>
                                <Descriptions.Item label="审核意见">
                                    <Input.TextArea rows={4} style={{maxWidth:"80%"}} value={record.userComments} onChange={this.changeInput}></Input.TextArea>
                                </Descriptions.Item>
                                <Descriptions.Item label="审核流程">
                                    {stepComponent(this.state.stepsData)}
                                </Descriptions.Item>
                            </Descriptions>
                        </div>
                    </div>
                    <div style={{textAlign:"center",margin:20}}>
                        <Button onClick={this.submit} type="primary" style={{margin:"0 20px"}}>提交</Button>
                        <Button onClick={() => {
                            // this.props.history.push("/auditManagement");
                            this.props.history.goBack();
                            }}>取消</Button>
                    </div>
                </Card>
                <Modal
                    visible={this.state.showPDF}
                    title={<span style={{fontWeight:"bold"}}>预览打印</span>}
                    width={1200}
                    bodyStyle={{padding:0,height:"70vh"}}
                    destroyOnClose={true}
                    footer={false}
                    onCancel={()=>{this.setState({showPDF:false})}}
                >
                    {/* <iframe scrolling='auto' title='预览' style={{border:'none',width:'100%',height:'500px'}} src={`http://${window.location.host}/ybt/form/dl/result/view?${record.processType === 2?'formId':'formTaskId'}=${record.businessId}`}></iframe> */}
                    <PreviewDoc id={record.businessId} type={record.processType === 2?'formId':'formTaskId'}/>
                </Modal>
                <Modal
                    visible={this.state.showFormListModal}
                    title="填报表单"
                    footer={false}
                    destroyOnClose={true}
                    onCancel={()=>{this.setState({showFormListModal:false})}}
                >
                    <FormListModal dataSource={this.state.planInfo.formList}/>
                </Modal>
                <Modal
                    visible={this.state.showPeopleListModal}
                    title="填报人员"
                    footer={false}
                    destroyOnClose={true}
                    onCancel={()=>{this.setState({showPeopleListModal:false})}}
                >
                    <PeopleListModal dataSource={this.state.planInfo.peopleList}/>
                </Modal>
                {
                    this.state.lookVisible && <Modal footer={null} visible={this.state.lookVisible} width={1200} onCancel={this.handleCancelLook}>
                    <AuditForm formTaskId={this.state.record.businessId}/>
                </Modal>
                }
                
            </div>
        )
    }
}


export default withRouter(Audit)