import React, { Component } from 'react';
import { Table, Modal } from 'antd';
import PreviewDoc from '@/pages/common/PreviewDoc';

export default class FormListModal extends Component {
    state = {
        showPDF:false,
        formId:"",
        columns:[
            {
                title:"序号",
                dataIndex:"sortIndex",
                width:80
            },{
                title:"表单名称",
                dataIndex:"formName",
                render:(text)=><span title={text}>{text}</span>
            },{
                title:"操作",
                render:(text,record)=>{
                    return <span style={{color:"#24695C",cursor:"pointer",margin:"0 15px"}} onClick={()=>{this._preview(record)}}>预览</span>
                }
            }
        ]
    }
    _preview = (record) => {
        console.log(record);
        this.setState({
            showPDF:true,
            formId:record.formId
        })
    }
    render() {
        const dataSource = this.props.dataSource;
        // const dataSource = [
        //     {sortIndex:"1",formId:"",formName:""}
        // ]
        return (
            <React.Fragment>
                <Table
                    rowKey="sortIndex"
                    dataSource={dataSource}
                    columns={this.state.columns}
                />
                <Modal
                    visible={this.state.showPDF}
                    title="预览"
                    width={1200}
                    bodyStyle={{padding:0}}
                    destroyOnClose={true}
                    footer={false}
                    onCancel={()=>{this.setState({showPDF:false})}}
                >
                    <iframe scrolling='auto' title='预览' style={{border:'none',width:'100%',height:'500px'}} src={`http://${window.location.host}/ybt/form/dl/result/view?formId=${this.state.formId}`}></iframe>
                </Modal>
                <Modal
                    visible={this.state.showPDF}
                    title={<span style={{fontWeight:"bold"}}>预览打印</span>}
                    width={1200}
                    bodyStyle={{padding:0,height:"70vh"}}
                    destroyOnClose={true}
                    footer={false}
                    onCancel={()=>{this.setState({showPDF:false})}}
                >
                    <PreviewDoc id={this.state.formId} type='formId'/>
                </Modal>
            </React.Fragment>
        )
    }
}
